import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel, BtnAddNew, BtnDelete } from 'components/common/button';
import { Row, Card, Table, Col, Form, Input, Popconfirm, Spin } from 'antd';
import { YearSemesterInput, DatePickerString, SelectAjax } from 'components/common';
import { getQueryParams } from 'utils/urlUtil';

import {
  getCodeStudent,
  getBonusType,
  getDisciplineBehavior,
  getDiscipline,
  getDisciplineById,
  addDiscipline,
} from 'services/AddDiscipline';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    approvalId: false,
    initialValue: {},
    listDataDiscipline: [],
    isPending: false,
  };

  componentDidMount = () => {
    const { id: approvalId } = getQueryParams();
    if (approvalId) {
      this.setState({ approvalId });
      this.getDisciplineById(approvalId);
    }
  };

  getFormData = () => {
    const tempObj = {
      students: {},
    };
    this.props.form.validateFields(async(err, values) => {
      Object.keys(values).forEach((value) => {
        const keyOfRow = value.split('-')[0];
        const id = value.split('-')[1];
        if(id) {
          if(!tempObj.students[id]) {
            tempObj.students[id] = {};
            tempObj.students[id].id = parseInt(id);
          }
          tempObj.students[id][keyOfRow] = values[value];
        } else {
          tempObj[keyOfRow] = values[value];
        }
      });
    });
    return tempObj;
  }

  getDisciplineById = async id => {
    try {
      this.setState({
        isPending: true,
      });
      const { form } = this.props;
      const disciplinesData = await getDisciplineById(id);
      const initialValue = handleInitialValue(disciplinesData.data);
      disciplinesData.data.students.forEach((item, index) => {
        item.key = index;
      });
      this.setState({
        initialValue,
        listDataDiscipline: disciplinesData.data.students,
      });
      form.setFieldsValue(initialValue);
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        isPending: false,
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    try {
      this.setState({
        isPending: true,
      });
      this.props.form.validateFields((err) => {
        const { listDataDiscipline, approvalId } = this.state;
        if (!err && listDataDiscipline.length) {
          const formDataTemp = this.getFormData();
          const students = [];
          Object.keys(formDataTemp.students).forEach((value, index) => { 
            const { bonusTypeId, disciplineId, disciplineBehaviorId, description } = formDataTemp.students[value];
            const formatStudent = {
              studentId: listDataDiscipline[index].id,
              classId: listDataDiscipline[index].classId,
              bonusTypeId,
              disciplineId,
              disciplineBehaviorId,
              description,
            };
            students.push(formatStudent);
          });
          formDataTemp.students = [...students];
          const params = {
            ...formDataTemp,
          }
          if(approvalId) {
            params.id = approvalId;
          }
          addDiscipline(params)
          .then(() => {
            this.props.history.goBack();
          })
          .catch(error => {
            console.log('error', error);
          });
        }
      });
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        isPending: false,
      });
    }
  };

  addDataAddReward = () => {
    const { listDataDiscipline } = this.state;
    const item = {
      key: listDataDiscipline.length ? listDataDiscipline[listDataDiscipline.length-1].key + 1 : 0,
      id: '',
      classId: '',
      disciplineId: '',
      disciplineBehaviorId: '',
      description: '',
    };
    listDataDiscipline.push(item);
    this.setState({
      listDataDiscipline,
    });
  };

  deleteData = key => {
    const { listDataDiscipline } = this.state;
    try {
      const tempListDataDiscipline = listDataDiscipline.filter(element => {
        return element.key !== key;   
      });
      this.setState({
        listDataDiscipline: [...tempListDataDiscipline],
      });
    } catch (err) {
      console.log(err);
    }
  };

  changeInputCode = (e, key) => {
    const { listDataDiscipline } = this.state;
    this.setState({
      isPending: true,
    });
    getCodeStudent(e.target.value)
      .then(res => {
        // eslint-disable-next-line array-callback-return
        listDataDiscipline.map((item) => {
          if (item.key === key) {
            item.id = res.data.id;
            item.fullname = res.data.firstname + res.data.lastname;
            item.dob = res.data.dob;
            item.className = res.data.className;
            item.classId = res.data.classId;
          }
        });
        this.setState({
          listDataDiscipline,
          isPending: false,
        });
      })
      .catch(error => {
        console.log('error', error);
        const { form } = this.props;
        listDataDiscipline.forEach(item => {
          if (item.key === key) {
            form.setFieldsValue({
              [`code-${key}`] : item.code,
            })
          }
        });
        this.setState({
          isPending: false,
        });
      });
  };

  render() {
    const {
      initialValue,
      isPending,
      listDataDiscipline,
    } = this.state;
    const { 
      getFieldDecorator, 
      getFieldValue,
      setFieldsValue
    } = this.props.form;
    const { approvalId } = this.state;
    const disabledSave =
      !listDataDiscipline ||
      listDataDiscipline.length === 0 ||
      listDataDiscipline.some(item => !item.id);
    const columns = [
      {
        title: 'M sinh vin',
        width: '200px',
        dataIndex: 'code',
        key: 'code',
        // eslint-disable-next-line no-unused-vars
        render: (_text, record) => {
          return (
            <Form.Item>
              {getFieldDecorator(`code-${record.key}`, {
                initialValue: record.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <Input onChange={e => this.changeInputCode(e, record.key)} />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'H tn',
        dataIndex: 'fullname',
        key: 'fullname',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Ngy sinh',
        dataIndex: 'dob',
        key: 'dob',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Lp',
        dataIndex: 'className',
        key: 'className',
        // eslint-disable-next-line no-unused-vars
      },
      {
        title: 'Cp k lut',
        dataIndex: 'parentBonusTypeId',
        key: 'parentBonusTypeId',
        width: '200px',
        render: (_text, record) => {
          return (
            <Form.Item>
              {getFieldDecorator(`parentBonusTypeId-${record.key}`, {
                initialValue: record.parentBonusTypeId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <SelectAjax 
                  service={getBonusType} 
                />
              )}
            </Form.Item>
          );
        },  
      },
      {
        title: 'Hnh vi k lut',
        dataIndex: 'disciplineBehaviorId',
        key: 'disciplineBehaviorId',
        width: '200px',
        render: (_text, record) => {
          return (
            <Form.Item>
              {getFieldDecorator(`disciplineBehaviorId-${record.key}`, {
                initialValue: record.disciplineBehaviorId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <SelectAjax 
                  service={getDisciplineBehavior} 
                />
              )}
            </Form.Item>
          );
        }, 
      },
      {
        title: 'Loi k lut',
        dataIndex: 'disciplineId',
        key: 'disciplineId',
        width: '200px',
        render: (_text, record) => {
          const parentBonusTypeId = getFieldValue(`parentBonusTypeId-${record.key}`);
          return (
            <Form.Item>
              {getFieldDecorator(`disciplineId-${record.key}`, {
                initialValue: record.disciplineId,
                rules: [
                  {
                    required: true,
                    message: 'Vui lng nhp thng tin!',
                  },
                ],
              })(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getDiscipline}
                  params={{ level: parentBonusTypeId }}
                  allowGetData={!!parentBonusTypeId}
                />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Ni dung x l',
        dataIndex: 'description',
        key: 'description',
        // width: '10%',
        render: (_text, record) => {
          return (
            <Form.Item>
              {getFieldDecorator(`description-${record.key}`, {initialValue: record.description})(
                <Input />
              )}
            </Form.Item>
          );
        },
      },
      {
        title: 'Thao tc',
        dataIndex: 'rewardType',
        render: (_text, record) => (
          <Popconfirm
            title="Bn c mun xa?"
            okText="Xa"
            cancelText="Hy"
            onConfirm={() => this.deleteData(record.key)}
          >
            <BtnDelete />
          </Popconfirm>
        ),
      },
    ];

    const optionFieldDecorator = {
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn lp!',
          },
        ],
      },
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lng chn nm hc!',
          },
        ],
      },
    };

    return (
      <Spin spinning={isPending}>
        <Card title={approvalId ? 'Cp nht nhp k lut' : 'Thm nhp k lut'}>
          <Row>
            <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSubmit}>
              <Col xs={24} sm={12} md={6}>
                <Form.Item label="S Q">
                  {getFieldDecorator('approvalNumber', {
                    initialValue: initialValue.approvalNumber,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={6}>
                <Form.Item label="Ngy QD">
                  {getFieldDecorator('approvalDate', {
                    initialValue: initialValue.approvalDate,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<DatePickerString placeholder="Chn ngy" />)}
                </Form.Item>
              </Col>
              <Col xs={24} sm={12} md={6}>
                <Form.Item label="Ni dung x l">
                  {getFieldDecorator('description', {
                    initialValue: initialValue.description,
                    rules: [
                      {
                        required: true,
                        message: 'Vui lng nhp thng tin!',
                      },
                    ],
                  })(<Input />)}
                </Form.Item>
              </Col>
              <YearSemesterInput form={this.props.form} optionFieldDecorator={optionFieldDecorator} />
              <Col xs={24} sm={12} md={6}>
                <Form.Item label=" ">
                  <BtnSave htmlType="submit" disabled={disabledSave} />
                  <BtnCancel onClick={() => this.props.history.goBack()} />
                </Form.Item>
              </Col>
            </Form>
          </Row>
          <Row>
            <BtnAddNew onClick={this.addDataAddReward} />
          </Row>
          <Row>
            <Col span={24}>
              <Table
                onRow={this.rowTable}
                columns={columns}
                dataSource={listDataDiscipline}
                rowKey="key"
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



